body {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#202B33), to(#4E5C6E));
  background-image: -webkit-linear-gradient(top, #202B33, #4E5C6E);
  background-image: -o-linear-gradient(top, #202B33, #4E5C6E);
  background-image: linear-gradient(to bottom, #202B33, #4E5C6E);
  background-attachment: fixed;
  height: 100vh;
  line-height: 1.4;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 100%;
}


